.dokan-promo-banner .thumbnail {
  display: flex;
}
.dokan-promo-banner .thumbnail svg {
  width: 227px;
  height: 147px;
}
.dokan-promo-banner .content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 1.5rem;
}
.dokan-promo-banner .content .details {
  flex: 0 0 170px;
}
.dokan-promo-banner .content .details h3 {
  color: #ffffff;
  font-size: 18px;
  font-weight: bold;
  font-style: normal;
  font-family: "SF Pro Text", sans-serif;
  margin: 0;
}
.dokan-promo-banner .content .details h3 .dokan-offer {
  display: none;
}
.dokan-promo-banner .content .details p {
  color: #bcbcbc;
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  font-family: "SF Pro Text", sans-serif;
  margin-bottom: 0;
}
.dokan-promo-banner .content .features {
  display: grid;
  column-gap: 2rem;
  row-gap: 1.25rem;
  align-items: center;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  flex-shrink: 0;
}
.dokan-promo-banner .content .features label {
  position: relative;
  color: #e1e1e1;
  font-size: 14px;
  font-style: normal;
  font-weight: normal;
  font-family: "SF Pro Text", sans-serif;
  padding-left: 34px;
  cursor: context-menu;
}
.dokan-promo-banner .content .features span {
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  border-radius: 3px;
  background-color: #3fc274;
}
.dokan-promo-banner .content .features span:after {
  content: "";
  position: absolute;
  left: 7px;
  top: 3px;
  width: 4px;
  height: 9px;
  border: solid white;
  border-width: 0 1.8px 1.8px 0;
  transform: rotate(45deg);
}
.dokan-promo-banner .content .features a {
  color: #999999;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  font-family: "SF Pro Text", sans-serif;
}
.dokan-promo-banner .content .features a:hover {
  color: #c9c9c9;
}
.dokan-promo-banner .btn-upgrade {
  flex-shrink: 0;
}
.dokan-promo-banner .btn-upgrade a {
  color: #ffffff;
  background: #2CBF67;
  font-size: 15px;
  font-weight: normal;
  font-style: normal;
  font-family: Lato, sans-serif;
  padding: 15px 28px;
  border-radius: 3px;
  text-decoration: none;
  transition: all 0.1s linear;
}
.dokan-promo-banner .btn-upgrade a:hover {
  background: #17a951;
}
.dokan-promo-banner .close-banner {
  position: absolute;
  top: 10px;
  right: 5px;
  border: 0;
  background: transparent;
  text-decoration: none;
}
.dokan-promo-banner .close-banner span {
  font-size: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #404040;
  transition: all 0.2s ease;
  cursor: pointer;
  border: 1px solid #3f3f3f;
  border-radius: 55px;
  width: 20px;
  height: 20px;
}
.dokan-promo-banner .close-banner span:hover {
  color: #f16982;
  border-color: #f16982;
}
@media only screen and (max-width: 576px) {
.dokan-promo-banner {
    flex-wrap: wrap;
    background-position: unset;
    background-image: unset;
    padding: 28px 23px;
    justify-content: start;
}
.dokan-promo-banner .thumbnail {
    display: none;
}
.dokan-promo-banner .content {
    flex-wrap: wrap;
    margin-bottom: 25px;
}
.dokan-promo-banner .content .details {
    text-align: left;
    flex: unset;
    margin-bottom: 20px;
}
.dokan-promo-banner .content .details p {
    width: 80%;
}
.dokan-promo-banner .content .details h3 {
    font-size: 15px;
}
.dokan-promo-banner .content .details h3 .dokan-offer {
    font-size: 13px;
    font-weight: 300;
    margin-left: 15px;
    display: inline-block;
}
.dokan-promo-banner .content .details h3 .penchant-off {
    margin: 0 5px;
    padding: 3px 8px;
    background-color: rgba(249, 95, 71, 0.15);
    color: #E04932;
    font-size: 15px;
    font-weight: 900;
    border-radius: 5px;
}
.dokan-promo-banner .content .details p {
    font-size: 13px;
}
.dokan-promo-banner .content .features {
    flex-wrap: wrap;
    row-gap: 0;
    column-gap: 1rem;
    grid-template-columns: repeat(2, minmax(0, 1fr));
}
.dokan-promo-banner .content .features .hidden-sm {
    display: none;
}
.dokan-promo-banner .content .features label {
    font-size: 13px;
    font-weight: 300;
}
.dokan-promo-banner .content .features a {
    margin-top: 20px;
    display: block;
    font-size: 13px;
}
.dokan-promo-banner .btn-upgrade a {
    display: block;
    padding: 12px 16px;
}
}
@media only screen and (max-width: 375px) {
.dokan-promo-banner .content .details p {
    width: 85%;
}
}
@media (min-width: 577px) and (max-width: 991px) {
.dokan-promo-banner .content .details {
    flex: 0 1 280px;
}
.dokan-promo-banner .content .features {
    display: none;
}
}
@media (min-width: 992px) and (max-width: 1120px) {
.dokan-promo-banner {
    background-position: bottom 20px right -10px;
}
.dokan-promo-banner .thumbnail svg {
    width: 180px;
    height: 140px;
}
.dokan-promo-banner .content {
    justify-content: center;
    column-gap: 0.5rem;
}
.dokan-promo-banner .content .details {
    flex: 0 0 180px;
}
.dokan-promo-banner .content .features {
    grid-template-columns: repeat(1, minmax(0, 1fr));
}
.dokan-promo-banner .content .features .hidden-sm {
    display: none;
}
}
@media (min-width: 1121px) and (max-width: 1300px) {
.dokan-promo-banner {
    background-position: bottom 15px right -20px;
}
.dokan-promo-banner .content {
    justify-content: center;
}
.dokan-promo-banner .content .details {
    flex: 0 0 250px;
}
.dokan-promo-banner .content .features {
    grid-template-columns: repeat(1, minmax(0, 1fr));
}
.dokan-promo-banner .content .features .hidden-sm {
    display: none;
}
}
@media only screen and (min-width: 1301px) {
.dokan-promo-banner {
    background-position: bottom 0 right -15px;
}
.dokan-promo-banner .content .details {
    flex: 0 0 170px;
}
.dokan-promo-banner .content .features {
    grid-template-columns: repeat(2, minmax(0, 1fr));
}
}
@media only screen and (min-width: 1550px) {
.dokan-promo-banner {
    background-position: bottom -5px right 165px;
}
.dokan-promo-banner .content .details {
    flex: 0 0 190px;
}
.dokan-promo-banner .content .features {
    grid-template-columns: repeat(3, minmax(0, 1fr));
}
}

.withdraw-requests .dokan-modal .modal-body {
  min-height: 130px;
}
.withdraw-requests .dokan-modal .modal-body textarea {
  width: 100%;
}
.withdraw-requests .image {
  width: 10%;
}
.withdraw-requests .seller {
  width: 20%;
}
.withdraw-requests td.seller img {
  float: left;
  margin-right: 10px;
  margin-top: 1px;
  width: 24px;
  height: auto;
}
.withdraw-requests td.seller strong {
  display: block;
  margin-bottom: 0.2em;
  font-size: 14px;
}
.withdraw-requests td.actions,
.withdraw-requests th.actions {
  width: 120px;
}
.withdraw-requests td.status span {
  line-height: 2.5em;
  padding: 5px 8px;
  border-radius: 4px;
}
.withdraw-requests td.status .approved {
  background: #c6e1c6;
  color: #5b841b;
